import "./../style/main.css";

import React, { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";

import useAsync from "../api/useAsync";
import { getSmartHome } from "../api/smart.home.js";

// 
import Chart from "chart.js";

const Main = () => {
  const chartRef = useRef(null);

  //  
  const [state] = useAsync(() => getSmartHome("1", "1", ""), [], false);
  const Data = state.data;

  const [smt_idx, setSmt_idx] = useState();
  const [bef_day, setBef_day] = useState();
  const [bef_week, setBef_week] = useState();
  const [bef_month, setBef_month] = useState();

  //  
  // const smtint = Number(smt_idx);
  // let smtintNum = Math.round(smtint * 100);
  const smtintNum = Number(smt_idx);

  useEffect(() => {
    //  
    if (Data !== null) {
      setSmt_idx(Data.smt_idx);
      setBef_day(Data.bef_day);
      setBef_week(Data.bef_week);
      setBef_month(Data.bef_month);
    }

    // 
    if (chartRef?.current) {
      const chartToDraw = chartRef.current.getContext("2d");

      new Chart(chartToDraw, {
        type: "bar",
        data: {
          labels: [" "],
          datasets: [
            {
              label: "",
              data: [smtintNum],
              backgroundColor: "#F49864",
              // hoverBorderColor: "rgba(68, 146, 251, 1)",
              hoverBorderWidth: 1,
              barThickness: 40,
            },
          ],
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,

          legend: {
            display: false,
          },
          // hover: {
          //   mode: "label",
          // },
          scales: {
            xAxes: [
              {
                // display: true,
                display: false,

                //    
                // offset: false,

                scaleLabel: {
                  display: true,
                  labelString: "",
                },
              },
            ],
            yAxes: [
              {
                // display: true,
                display: false,
                ticks: {
                  beginAtZero: true,
                  steps: 10,
                  stepValue: 1,
                  max: 100,
                },
              },
            ],
          },
          annotation: {
            drawTime: "afterDatasetsDraw", // (default)
            events: ["click"],
            dblClickSpeed: 350, // ms (default)
            annotations: [
              {
                drawTime: "afterDraw",
                id: "a-line-1",
                type: "line",
                mode: "horizontal",
                scaleID: "y-axis-0",
                value: "25",
                borderColor: "red",
                borderWidth: 2,
              },
            ],
          },
        },
      });
    }
  });

  // 
  function stepTemper() {
    if (0 <= smtintNum && smtintNum <= 20) {
      return (
        <>
          <div>
            <img src="./image/freeze.png" />
          </div>
          <div>
            <h4></h4>
            <p>
                .
              <br />
               !
            </p>
          </div>
        </>
      );
    } else if (20 < smtintNum && smtintNum <= 40) {
      return (
        <>
          <div>
            <img src="./image/feer.png" />
          </div>
          <div>
            <h4></h4>
            <p>
               
              <br />
                .
            </p>
          </div>
        </>
      );
    } else if (40 < smtintNum && smtintNum <= 60) {
      return (
        <>
          <div>
            <img src="./image/stability.png" />
          </div>
          <div>
            <h4></h4>
            <p>
                
              <br />
                 
            </p>
          </div>
        </>
      );
    } else if (60 < smtintNum && smtintNum <= 80) {
      return (
        <>
          <div>
            <img src="./image/increase.png" />
          </div>
          <div>
            <h4></h4>
            <p>
                
              <br />
               .
            </p>
          </div>
        </>
      );
    } else if (80 < smtintNum && smtintNum <= 100) {
      return (
        <>
          <div>
            <img src="./image/overhit.png" />
          </div>
          <div>
            <h4></h4>
            <p>
               
              <br />
               !
            </p>
          </div>
        </>
      );
    }
  }

  // , ,  
  function prepare(cts) {
    if (cts > 0) {
      return (
        <div style={{ color: "#c94f4f", fontWeight: "700" }}>
          <p style={{ fontSize: "24px" }}>+{cts}</p>
          <p style={{ fontSize: "14px" }}></p>
        </div>
      );
    } else if (cts < 0) {
      <div style={{ color: "#4492fb", fontWeight: "700" }}>
        <p style={{ fontSize: "24px" }}>{cts}</p>
        <p style={{ fontSize: "14px" }}></p>
      </div>;
    } else {
      <div style={{ color: "#8e8e8e", fontWeight: "700" }}>
        <p style={{ fontSize: "24px" }}>{cts}</p>
        {/* <p style={{ fontSize: "14px" }}></p> */}
      </div>;
    }
  }

  //   
  function circleTemper() {
    if (0 <= smtintNum && smtintNum <= 20) {
      return (
        <div style={{ background: "#679A40" }}>
          <span>{smtintNum}</span>
        </div>
      );
    } else if (20 < smtintNum && smtintNum <= 40) {
      return (
        <div style={{ background: "#4BC2C2" }}>
          <span>{smtintNum}</span>
        </div>
      );
    } else if (40 < smtintNum && smtintNum <= 60) {
      return (
        <div style={{ background: "#679A40" }}>
          <span>{smtintNum}</span>
        </div>
      );
    } else if (60 < smtintNum && smtintNum <= 80) {
      return (
        <div style={{ background: "#F49864" }}>
          <span>{smtintNum}</span>
        </div>
      );
    } else if (80 < smtintNum && smtintNum <= 100) {
      return (
        <div style={{ background: "#C94F4F" }}>
          <span>{smtintNum}</span>
        </div>
      );
    }
  }
  return (
    <div>
      <div className="thermometer">
        <h3>  </h3>
        <div className="thermometer_bg">
          <span />
          <span />
        </div>

        <div className="chartWrap">
          <canvas id="myChart" ref={chartRef} height="100%" width="100%" />
        </div>
        {circleTemper()}
      </div>
      <div className="thermometerCts">
        <img src="./image/thermometerCts.png" />
      </div>

      <div className="cts">
        <div className="btn">
          <Link to="chart" target="_blank">
             
          </Link>
        </div>

        <div className="temperatureStep">
          <div className="tit">
            <h3></h3>
            <div>
              <Link to="manual">
                <img src="./image/question.png" />
              </Link>
            </div>
          </div>

          {/*    */}
          <div className="titcts">{stepTemper()}</div>
        </div>

        <div className="compare">
          <div>
            <p> </p>
            {prepare(bef_day)}
          </div>
          <div>
            <p> </p>
            {prepare(bef_week)}
          </div>
          <div>
            <p> </p>
            {prepare(bef_month)}
          </div>
        </div>

        <div className="footer">
          <span>Last updated Oct 27 2022</span>
          <Link to="manual">
            <img
              src="./image/management_en_h_c.svg"
              alt=" "
            />
          </Link>
        </div>

        <div className="copyright">
          <span>COPYRIGHT (C) () ALL RIGHTS RESERVED.</span>
          <span>
               ,    
                     .
          </span>
          {/* <img src="./image/copyright.png" /> */}
        </div>
      </div>
    </div>
  );
};
export default Main;
